/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
@import 'variables';
/* Front page */

.blog,.error404,.page-id-25,.single-post,.search-results,.search-no-results{
  .site-content{
    display: flex;
    margin-top: 48px;
  }
  #primary{
  width: 75%;
  padding: 50px 80px;
  }
  #secondary{
    width: auto;
    padding: 50px 50px;
    border-left: 1px solid #e4e4e4;
    a{
        text-decoration: none;
        color: #248A83;
      }
      i{
        min-width: 35px;
        padding: 0 5px;
        // color: black;
      }
    aside {
      .widget-title{
        text-transform: uppercase;
        font-family: "no";
      }
      ul{
      list-style-type: none;
      margin: 0;
      padding: 0;
    }
    } 
  }
}

//journal page
.blog,.single-post{
  .site-content{
    
    article .entry-header{
    
    position: relative;
    text-transform: uppercase;
    img{
      width: 100%;
    }
    .entry-title{
      color: white;
      font-family: "no", sans-serif;
      position: absolute;
      top: 10px;
      left:0;
      max-width: 80%;
      background: #248A83;
      padding: 10px;
      a{
        text-decoration: none;
        font-family: "no";
        color: white;
        width: 100%;
      } 
    }
    .entry-meta{
      position: absolute;
      bottom: 6px;
      right: 0;
      color: white;
      background: #248A83;
      padding: 5px;
      a{
        color: white;
        text-decoration: none;
      }
    }
  }
  .entry-footer{
    margin-top: 40px;
    text-transform: uppercase;
    font-family: "no", sans-serif;
      span{
        margin: 20px 20px 10px 0;
        a{
        color: #248A83;
        text-decoration: none;
      }
      }
    } 
    .single-post-social-media{
      span{
        padding: 10px 20px;
        border: 1px solid black;
        text-transform: uppercase;
        font-family: "no", sans-serif;
        a{
          color: black;
          text-decoration: none;
        }
        i{
          padding: 10px;
        }
        &:hover{
          background: #248A83;
          a{
            color: white;
          }
        }
        
      }
    }
  }

}


body{
  font-family: $font__main;
}
// FOOTER
.site-footer{
  color: white;
  .footer-background-image{
    height: 300px;
    .site-info{
    display: flex;
    padding: 0 150px;
    height: 75%;
    h3{
      font-family: $font__uppercase;
      text-transform: uppercase;
      color: $text-color_orange;
      }
    .contact-info,.business-hours{
      // height: 75%;
      width: 20vw;
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .contact-info{
      a{
        text-decoration: none;
        color: white;
      }
      i{
        padding: 0 7px;
        font-size: 1.5rem;
      }
    }
    .business-hours{
      .text-bold{
        font-weight: bold;
      }
    }
    .footer-image{
      padding: 50px 0 50px 200px;
      width: auto;
      a{
        text-decoration: none;
        color: white;
      }
    }
    
  }
  .footer-copyright{
    font-family: "no",sans-serif;
    text-transform: uppercase;
    text-align: center;
    padding: 10px;
  }
  }
  
}